import {Component, OnInit} from '@angular/core';
import {DatabaseService} from '../database.service';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-kmitc',
  templateUrl: './kmitc.component.html',
  styleUrls: ['./kmitc.component.css']
})
export class KmitcComponent implements OnInit {
  constructor(private service: DatabaseService,
              private toastr: ToastrService) {
  }

  users: any;
  giveAccess = false;
  kmit = [];

  private abcdef = 'Kmit123$%^';


  title = 'Keshav Memorial Institute of Technology';

  ngOnInit(): void {
    this.service.getUsers().subscribe(response => {
      // console.log("Response:",response);
      this.users = response;
      this.kmit = this.users.user;
      // console.log("Users KMIT Any ",this.kmit);
    });
  }

  check(e: HTMLInputElement) {
    if (e.value == this.abcdef) {
      this.giveAccess = true;
      this.toastr.success('Congrats Unlocked All the Users', 'KMIT All Users Unlocked');
    } else {
      this.toastr.error('You have entered wrong password', 'Don\'t try Again');
      alert('Wrong Place Boii');
    }
  }

}
